'use strict';

// MODULES //

import isinfinitef from '@stdlib/math-base-assert-is-infinitef';
import isnanf from '@stdlib/math-base-assert-is-nanf';
import lnf from '@stdlib/math-base-special-lnf';
import f32 from '@stdlib/number-float64-base-to-float32';


// VARIABLES //

var ZERO = f32( 0.0 );
var ONE = f32( 1.0 );
var HALF = f32( 0.5 );


// MAIN //

/**
* Computes the hyperbolic arctangent of a single-precision floating-point number.
*
* @param {number} x - input value
* @returns {number} hyperbolic arctangent (in radians)
*
* @example
* var v = atanhf( 0.0 );
* // returns 0.0
*
* @example
* var v = atanhf( 0.9 );
* // returns ~1.472
*
* @example
* var v = atanhf( 1.0 );
* // returns Infinity
*
* @example
* v = atanhf( -1.0 );
* // returns -Infinity
*
* @example
* var v = atanhf( NaN );
* // returns NaN
*/
function atanhf( x ) {
	x = f32( x );
	if ( x === ZERO ) {
		return x;
	}
	if ( isnanf( x ) || isinfinitef( x ) ) {
		return NaN;
	}
	return f32( HALF * lnf( f32( f32( ONE+x ) / f32( ONE-x ) ) ) );
}


// EXPORTS //

export default atanhf;
